.App {
  text-align: center;
  background-color: skyblue;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #0055ff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: red;
  text-decoration: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* just add this */
.navbar {
  overflow: hidden;
  background-color: skyblue;
  position: fixed;
  top: 0;
  width: 100%;

}

.navbar a {
  float: right;
  display: block;
  color: black;
  text-align: center;
  padding: 15px 18px;
  text-decoration: none;
  font-size: 18px;
}

.navbar a:hover {
  color: #0055ff;
}

.container {
  padding: 18px;
  margin-top: 35px;
  height: 2000px;
}

.div1 {
  position: absolute;
  width: 40%;
  margin-left: 18px;
  margin-top: 10px;
  border: outset 2px;
  background: #0055ff;
  border-radius: 15px;
}

.div2 {
  position: relative;
  float: right;
  width: 40%;
  margin-right: 18px;
  margin-top: 10px;
  margin-bottom: 15px;
  border: outset 2px;
  background: #0055ff;
  border-radius: 15px;
}

.cost {
  padding: 3px;
  position: relative;
  width: 40%;
  margin-left: 375px;
  margin-top: 275px;
  margin-bottom: 15px;
  border: outset 2px;
  background: #0055ff;
  border-radius: 15px;
}

.customers {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 40%;
  margin-left: 375px;
  margin-bottom: 15px;
  border-radius: 15px;
}

.customers td, .customers th {
  border: 2px solid black;
  padding: 8px;
}

.customers tr:nth-child(even){background-color: #f2f2f2;}

.customers tr:hover {background-color: lightgrey;}

.customers th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: #0055ff;
  color: black;
}

.para {
  background-color: #0055ff;
  width: auto;
}